<template>
	<div>
		<Header title="" type="0" route="/homeIndex" :casLogin="casLogin"></Header>
		<div class="bod">
			<p class="class"></p>
			<NOdata title="" v-if="resetList.length == 0 && dataList.length == 0"></NOdata>
			<Menu :title="item.name" v-for="item in resetList" :key="item.id" :route="item.path"/>
			<!--  -->
		 	<!-- <Menu :title="item.name" v-for="(item,index) in dataList" :key="index" :route="item.path"/> -->
		</div>
	</div>
</template>

<script>
import Menu from '../../common/menu'
import { Api } from '@/utils/axios'
	export default{
		data(){
			return{
				Identity:sessionStorage.getItem('Identity'),
				casLogin:sessionStorage.getItem('casLogin'),
				changeList:[
					{name:'',path:'/teacher/examine/adjustList',url:'js_sh_tk'},
					{name:'',path:'/teacher/examine/borrowingList',url:'js_sh_js'},
					{name:'',path:'/teacher/examine/searchScore',url:'js_sh_cj'},
					{name:'',path:'/teacher/guideIndex',url:'js_sh_pyfa'},
					{name:'',path:'/teacher/courseAudit?type=0',url:'js_sh_xzkc'},
					{name:'',path:'/teacher/courseAudit?type=1',url:'js_sh_kksq'},
          {name:'',path:'/teacher/bookIndex?id=2', url:'js_sh_xsxxhd'},
          {name:'',path:'/teacher/bookIndex?id=3', url:'js_sh_bysxx'},
					{name:'',path:'/teacher/bookIndex?id=4', url:'js_sh_xjyd'},
					{name:'',path:'/teacher/verifyIndex',url:'js_sh_jhwt'},
					{name:'',path:'/teacher/bookIndex?id=11',url:'js_sh_sysjy'},
					{name:'',path:'/teacher/bookIndex?id=12',url:'js_sh_jxjd'},
					{name:'',path:'/teacher/bookIndex?id=0',url:'js_sh_smsq'},
					{name:'',path:'/teacher/bookIndex?id=1',url:'js_sh_xsyszd'}, 
					{name:'',path:'/teacher/bookIndex?id=5',url:'js_sh_cxssq'},
					{name:'',path:'/teacher/bookIndex?id=6',url:'js_sh_xshk'},
					{name:'',path:'/teacher/bookIndex?id=7',url:'js_sh_cjqr'},
					{name:'',path:'/teacher/bookIndex?id=8',url:'js_sh_cjcj'},
					{name:'',path:'/teacher/bookIndex?id=9',url:'js_sh_cjrd'},
					{name:'',path:'/teacher/bookIndex?id=10',url:'js_sh_cjxg'}
				],
				resetList:[],
				// 
				dataList:[
					{name:'',path:'/teacher/verifyIndex'},
					{name:'',path:'/teacher/bookIndex?id=11'},
					{name:'',path:'/teacher/bookIndex?id=12'},
					{name:'',path:'/teacher/bookIndex?id=0'},
					{name:'',path:'/teacher/bookIndex?id=1'},
					{name:'',path:'/teacher/bookIndex?id=5'},
					{name:'',path:'/teacher/bookIndex?id=6'},
					{name:'',path:'/teacher/bookIndex?id=7'},
					{name:'',path:'/teacher/bookIndex?id=8'},
					{name:'',path:'/teacher/bookIndex?id=9'},
					{name:'',path:'/teacher/bookIndex?id=10'},
				],
				i_id:''
			}
		},
		methods:{
			handle(e){
				this.$router.push(e.path)
			},
			async Change(){
				const params = {
					xx0101id: sessionStorage.getItem('schoolCode') || '',
					type: this.Identity == '2' ? '0' : '',
					isqy: '',
					key: 'qzkj'
				}
				const res = await Api.getHomeMenuApi(params)
				if(res.data.length > 0){
					res.data.forEach(item => {
						if(item.url == 'js_sh'){
							this.i_id = item.i_id
						}
					});
				}
				this.getAuth()
			},
			async getAuth(){
				const params = {
					I_ID:this.i_id
				}
				const res = await Api.getSubMenu(params)
				this.resetList = res.data
				if(res.data.length > 0){
					this.resetList.forEach(item => {
						this.changeList.forEach(list => {
							if(item.url == list.url){
								item.path = list.path
							}
						});
					});
				}
			},
			fn1(item){
				this.$router.push({
					path: item.path,
					query: {
						type:item.url == 'js_sh_kksq' ? '1' : item.url == 'js_sh_xzkc' ? '0' : ''
					}
				})
			}
		},
		created(){
			this.Change()
		},
		components:{
			Menu
		}
	}
</script>

<style scoped lang="less">
	.bod{
		margin: 60px auto 0;
		.class{
			color: @attendance-background-color-1;
			font-size: 18px;
			padding: 0 1.25rem 1.25rem;
			font-weight:600;
		}
	}

</style>



