<template>
  <div>
    <Header title=""></Header>
    <Menu 
      :title="item.name" 
      v-for="(item,index) in menus" :key="index" 
      :route="item.path"/>
  </div>
</template>

<script>
import Menu from '../../common/menu'
  export default {
    data(){
      return{
        menus:[
          {name:'',path:'/student/courseAdd?type=0'},
          {name:'',path:'/student/courseAdd?type=1'},
          {name:'',path:'/student/courseSet'},
        ]
      }
    },
    components:{
      Menu
    }
  }
</script>



