import service from './request'
import {baseURL} from './baseUrl.js'
const parseFormDataParams = (data) => {
  const formData = new FormData()
  for (const name in data) {
    if (data.hasOwnProperty(name)) {
      if (data[name]) {
        formData.append(name, data[name])
      }
    }
  }
  return formData
}
